import { GET_WALLETS, DELETE_WALLETS, GET_WALLET,GET_TRANSACTIONS,GET_TRANSACTION,DELETE_TRANSACTION } from "../actions/types";

const initialState = {
  wallets: [],
  wallet: ' ',
  transactions :[],
  transaction: ' ' 
};

export default function (state = initialState, action) {
  switch (action.type) {
    case GET_WALLETS:
      return { ...state, wallets: action.payload };
    case DELETE_WALLETS:
      return {
        ...state,wallets: state.wallets.filter((x) => x.id !== action.payload)};
    case GET_WALLET:
      return { ...state,wallet:action.payload};

//Transactionss

      case GET_TRANSACTIONS:
        return { ...state, transactions: action.payload };
        case GET_TRANSACTION:
          return{...state, transaction: action.payload }
          case  DELETE_TRANSACTION:
            return { ...state, transactions: state.transactions.filter(x => x.id !== action.payload) }
       
          default:
      return state;
  }
}
