import React from "react";
import "./App.css";
import Nav from "./Components/Shared/Nav";
import "bootstrap/dist/css/bootstrap.min.css";
import Welcome from "./Components/Welcome";
import { BrowserRouter, Route, Switch } from "react-router-dom";
import dashboard from "./Components/dashboard/dashboard";
import CreateWallet from "./Components/dashboard/dashboardoperations/CreateWallet";
import UpdateWallet from "./Components/dashboard/dashboardoperations/UpdateWallet";
import NotFound from "./Components/Shared/NotFound";
import { Provider } from 'react-redux';
import store from './Store';
import transaction from "./Components/dashboard/transactions/transaction";
import AddTransaction from "./Components/dashboard/transactions/transactionoperations/AddTransaction";
import updateTrans from "./Components/dashboard/transactions/transactionoperations/updateTrans";
import 'react-app-polyfill/stable'



function App() {
  return (
    <Provider store={store}>
    <BrowserRouter>
      <Route path="/" component={Nav} />
      <Switch>
        <Route path="/" exact component={Welcome} />
        <Route path="/dashboard" exact component={dashboard} /> 

        <Route path="/createwallet" exact component={CreateWallet} />   
       
        <Route path="/updatewallet/:id" exact component={UpdateWallet} />   
       
        <Route path="/transactions/:id" exact component={transaction} />   

<Route path="/trans/add/:id" exact component={AddTransaction}/>

<Route path="/trans/update/:id/:Transid" exact component={updateTrans}/>

       <Route path="/" component ={NotFound} />
        </Switch>
    </BrowserRouter>
    </Provider>
  );
}

export default App;
