import React, { Component } from 'react'

import { Link } from 'react-router-dom'
import { updateTransaction , getTransaction ,getWallet} from '../../../../actions/projectactions'
import { connect } from 'react-redux'

 class updateTrans extends Component {

    constructor(props) {
        super(props)
    
        this.state = {
            id:'',
            amount: '',
            description: '',
            type: '1',
    
        }
        this.name = ' '
        this.transactionDate = ''
    }
    
    componentWillReceiveProps(nextProps) {

        if (nextProps.transaction) {
            
            this.setState({

                id: nextProps.transaction.id,
                amount: nextProps.transaction.amount,
                description: nextProps.transaction.description,
                type: nextProps.transaction.type,
                transactionDate:nextProps.transaction.transactionDate

            })
        }
                this.name = nextProps.wallet.name
                this.transactionDate=nextProps.transaction.transactionDate

                if(nextProps.wallet){
this.setState({
    currentbalance : nextProps.wallet.currentbalance
})

                }
            }
        
        

        changeHandler = (event, fieldName) => {
            this.setState({
                [fieldName]: event.target.value
            })
        }


        handleSubmit = (event) => {

        if (this.state.type === "2") {
            
            if (this.state.currentbalance < this.state.amount) {
                alert("Account balance is not enough to make this transaction..!!!")
            }else {
    
        let updateTransaction = {
            amount:this.state.amount,
            description:this.state.description,
            type:this.state.type,
            transactionDate:this.transactionDate
        
    }
    this.props.updateTransaction(this.state.id,this.props.match.params.id,updateTransaction, this.props.history)
    
        event.preventDefault();
      }
    }else{
        let updateTransaction = {
            id:this.state.id,
            amount: this.state.amount,
            description: this.state.description,
            type: this.state.type,
            transactionDate:this.transactionDate
        }
    
        this.props.updateTransaction(this.state.id,this.props.match.params.id,updateTransaction, this.props.history)
    
    
        event.preventDefault();
    
        
    }
}


        componentDidMount() {
            this.props.getTransaction(this.props.match.params.id, this.props.match.params.Transid)
            this.props.getWallet(this.props.match.params.id)
        }

    render() {
        const wallet = this.props.wallet;
        let id = this.props.match.params.id;
        const { amount, description,type } = this.state;
      
        return (

            <div className="add-PBI">
            <div className="container">
                <div className="row">
                    <div className="col-md-8 m-auto">
                        <Link to={`/transactions/${id}`} className="btn btn-light">
                            Back to Wallet
                        </Link>
                        <h4 className="display-4 text-center">Update Transaction</h4>
                        <h2>For Account Name : {wallet.name}</h2>
                        <form onSubmit={this.handleSubmit}>
                            <div className="form-group">
                                <input type="number" min="1" value={amount} onChange={event=>this.changeHandler(event,"amount",false)} className="form-control form-control-lg" placeholder="Amount" />
                            </div>
                            <div className="form-group">
                                <textarea value={description} onChange={event=>this.changeHandler(event,"description",false)} className="form-control form-control-lg" placeholder="Description"/>
                            </div>
                            <div className="form-group">
                                <label htmlFor="exampleFormControlTextarea1">Transaction Type : </label>
                                <div className="form-check form-check-inline">
                                    <input checked className="form-check-input" type="radio"  id="income" name="type" onChange={event=>this.changeHandler(event,"type",false)} value="1"  />
                                    <label className="form-check-label" htmlFor="income">Income</label>
                                </div>
                                <div className="form-check form-check-inline">
                                    <input  className="form-check-input" type="radio"  id="expense" name="type" onChange={event=>this.changeHandler(event,"type",false)}  value="2" />
                                    <label className="form-check-label" htmlFor="expense">Expense</label>
                                </div>
                            </div>
                        
                            <input type="submit" className="btn btn-primary btn-block mt-4" />
                        </form>
                    </div>
                </div>
            </div>
        </div>

        )
    }
}

const mapStateToProps = (state) => ({
    errors: state.errors,
    transaction: state.wallet.transaction,
    wallet: state.wallet.wallet

});
export default connect(mapStateToProps, {updateTransaction, getTransaction ,getWallet})(updateTrans)
