import React, { Component } from "react";
import { Link } from "react-router-dom";
import DashBoardItem from "./DashBoardItem";

import {connect} from 'react-redux'
import {getWallets} from '../../actions/projectactions'

class dashboard extends Component {

  constructor(props) {
    super(props)
  
    this.state = {
      currentbalance:0.0
    }
  }
  
componentWillReceiveProps(nextProps){
  if(nextProps.wallets){
    let totalBal = 0
    
for(let i=0;i<nextProps.wallets.length;i++){
  totalBal = totalBal+nextProps.wallets[i].currentbalance
}

    this.setState({currentbalance: totalBal})
  }
}


componentDidMount(){
this.props.getWallets()

}

  render() {
const wallets = this.props.wallets
const walletComponent = wallets.map(wallet => (<DashBoardItem key={wallet.id} wallet = {wallet}/>))

    return (
      <div className="projects">
        <div className="container">
          <div className="row">
            <div className="col-md-12">
              <h1 className="display-4 text-center">My Wallets</h1>
              <br />
              <div className="btn-group">
                <button
                  type="button"
                  className="btn btn-info btn-lg dropdown-toggle"
                  data-toggle="dropdown"
                  aria-haspopup="true"
                  aria-expanded="false"
                >
                  Create new
                </button>
                <div className="dropdown-menu">
                  <Link className="dropdown-item" to="createwallet">
                    Wallet
                  </Link>
                </div>
         
              </div>
                       
              <br />
              <div className="card text-center">
                <div className="card-header bg-success text-white">
                  <h4>Current Balance (Total)</h4>
                  <h1>Rs : {this.state.currentbalance}</h1>
                  
                </div>
              </div>
              <hr />
            
           {walletComponent}

            </div>
          </div>
        </div>
      </div>
    );
  }
}


const mapStateToProps = (state) => ({

wallets : state.wallet.wallets
})
export default connect(mapStateToProps,{getWallets}) (dashboard);
